	module.exports = {"data":{"markdownRemark":{"html":"<p>As humans we are pretty useless animals, not really any natural skills - but we do have a highly refined ability to make use of tools. This didn't stop with the wheel, sundials, or  even computers and the internet… there is always a next step.</p>\n<p>That is far too grand of an introduction to this post; however the Echo Wall Clock is the best £30 you can spend right now.</p>\n<p><img src=\"/images/echo-clock.jpg\" alt=\"Amazon Echo Wall Clock\"></p>\n<p>The <a href=\"https://www.amazon.co.uk/Amazon-Echo-Wall-Clock/dp/B07FNPNNHH\">Amazon Echo Wall Clock</a> is a £29.99 basic wall clock that takes 2 AA batteries and hangs off a nail like any other cheap clock. However, it has a few tricks up its sleeve…</p>\n<p>Firstly it's internet connected via Bluetooth to your nearest Echo device - meaning the time is always correct. Daylight savings ends and those hands swing around to correct itself! But the killer feature here is timers…</p>\n<p>It's hard to explain how great this is, but it really does make it the perfect kitchen clock. When cooking there are many times to consider, everything has its own cooking time; times to take things out, put things in, stir things… it can be hard to keep track. This all got easier a few years ago with the introduction of Alexa style devices which could set multiple timers just by asking. </p>\n<p>This led to a lot of questions… “Alexa, how long's left?”, “Alexa, how long is left on my pasta timer?”, “Alexa, do I have any timers?”…. the same old boring questions.</p>\n<p>The Echo Wall Clock changes all that. With 60 light up sections around the edge of the clock your timers are perfectly visualised, easy to glance at while cooking. As many timers as you can think to set, the clock will happily display and track for you. It even flashes along with the beeps Alexa makes to announce a timer is complete.</p>\n<p>If you’re in need of a new kitchen clock, have an Echo, and cook a lot… I'd strongly recommend this device!</p>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"https://www.youtube.com/embed/Za72fHCQyg0\" frameborder=\"0\" allow=\"accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>","timeToRead":1,"excerpt":"As humans we are pretty useless animals, not really any natural skills - but we do have a highly refined ability to make use of tools. This…","frontmatter":{"title":"Echo Wall Clock","cover":"https://unsplash.it/1152/300/?random?clocks","date":"2019-09-23T21:13","tags":["clock","tech","alexa","echo"],"author":"eddie"},"fields":{"slug":"/echo-wall-clock"}},"prev":{"excerpt":"The whois record for this website tells me I registered the domain name on March 11th 2012 at 21:56 - over 1…","frontmatter":{"title":"Wasted effort","cover":"https://unsplash.it/1152/300/?random?WastedEffort","date":"2013-07-31T22:59"},"fields":{"slug":"/wasted-effort"}},"next":{"excerpt":"","frontmatter":{"title":"When Your Heart Stops Beating","cover":"https://unsplash.it/1152/300/?random?emo","date":"2019-09-22T21:48"},"fields":{"slug":"/wyhsb"}},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"slug":"/echo-wall-clock","total":50,"prev":"/wasted-effort","next":"/wyhsb"}}

